<template>
  <div class="border rounded px-4 pt-4 pb-3 mb-4">
    <div class="d-flex jutify-content-between">
      <h3 class="mb-0">Assessment Format</h3>
      <h4 class="ml-auto mb-0"><span class="font-size-sm mr-2 text-gray-700">Total Questions :</span>{{ this.total_questions_count }}</h4>
    </div>
    <p class="mb-0">Candidate evaluation would be based on the following types of questions.</p>

    <hr class="my-3 pb-1" />
    <div class="row">
      <div cl v-for="(count, column_name, index) in question_count" :key="index">
        <b-card class="shadow-none px-2 mx-2 mb-3" body-class="d-flex align-items-center px-0 py-2">
          <img :src="icons[column_name]" class="mr-2 img-thumbnail p-0" />
          <span class="mr-2"
            ><span>{{ question_type_labels[column_name] }}</span> :</span
          ><span class="font-weight-bold">{{ count }}</span>
        </b-card>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    props: { categories: Array },
    data() {
      return {
        question_count: {},
        total_questions_count: 0,
        icons: {
          "Short Text": "/img/ninjainterview/short-text.svg",
          "Text Area": "/img/ninjainterview/text-area.svg",
          Numerical: "/img/ninjainterview/numerical.svg",
          Boolean: "/img/ninjainterview/boolean.svg",
          "Multiple Choice": "/img/ninjainterview/multiple-choice.svg",
          "File Upload": "/img/ninjainterview/file-upload.svg",
          "Voice Recording": "/img/ninjainterview/audio-recording.svg",
          "Video Recording": "/img/ninjainterview/video-recording.svg",
          "Code Editor": "/img/ninjainterview/code-editor.svg",
        },
        question_type_labels: {
          "Short Text": "Short Text (Single Line)",
          "Text Area": "Paragraph (Long Text)",
          Numerical: "Number",
          Boolean: "Boolean (Yes/No)",
          "Multiple Choice": "Multiple Choices (List)",
          "File Upload": "File Upload",
          "Voice Recording": "Audio Recording",
          "Video Recording": "Video Recording",
        },
      };
    },
    mounted() {
      this.getQuestionsCount(this.categories);
    },
    methods: {
      // Navigate to instruction tab
      nextStep() {
        this.$emit("next", "instruction", 2);
      },
      // Get each questions type count
      getQuestionsCount(categories) {
        categories.forEach((category) => {
          this.total_questions_count = this.total_questions_count + category.questions.length;
          category.questions.forEach((question) => {
            if (!this.question_count[question.type]) this.question_count[question.type] = 0;
            this.question_count[question.type]++;
          });
        });
      },
    },
  };
</script>
